import axios from 'axios';
import { toast } from 'react-toastify';
import { getToken } from 'utils/token.localstorage';

export const deleteFile = async ({ images }: { images: string[] }) => {
  try {
    const token = getToken();

    const res = await axios.put<string[]>(
      '/api/posts/upload',
      { images },
      {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      }
    );

    return res.data;
  } catch (error: any) {
    console.log(error);

    if (error?.response?.status === 401) {
      toast.error('ÌååÏùº ÏÇ≠Ï†ú Ï§ë ÏóêÎü¨Í∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§. Îã§Ïãú ÏãúÎèÑÌï¥Ï£ºÏÑ∏Ïöî');
      window.location.reload();
    }
  }
};

export const postFileUpload = async ({ formData }: { formData: FormData }) => {
  try {
    const token = getToken();

    const res = await axios.post<string[]>('/api/posts/upload', formData, {
      headers: {
        'Content-Type': 'multipart/form-data',
        Authorization: `Bearer ${token}`,
      },
    });

    return res.data;
  } catch (error: any) {
    if (error?.response?.status === 401) {
      toast.error('ÏóÖÎ°úÎìúÏ§ë ÏóêÎü¨Í∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§. Îã§Ïãú ÏãúÎèÑÌï¥Ï£ºÏÑ∏Ïöî');
      window.location.reload();
    }
  }
};

export const profileFileUpload = async ({
  formData,
}: {
  formData: FormData;
}): Promise<string> => {
  try {
    const token = getToken();

    const res = await axios.post<string>('/api/users/edit/avatar', formData, {
      headers: {
        'Content-Type': 'multipart/form-data',
        Authorization: `Bearer ${token}`,
      },
    });

    return res.data;
  } catch (error: any) {
    if (error?.response?.status === 401) {
      toast.error('ÏóÖÎ°úÎìúÏ§ë ÏóêÎü¨Í∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§. Îã§Ïãú ÏãúÎèÑÌï¥Ï£ºÏÑ∏Ïöî');
      window.location.reload();
    }
    return '';
  }
};
