import imageCompression from 'browser-image-compression';
import { useState } from 'react';

const useImageCompress = () => {
  const [isLoading, setIsLoading] = useState(false);

  const compressImage = async (imageFile: File) => {
    if (isLoading) return;

    setIsLoading(true);
    console.log(`ÏõêÎ≥∏ Ïù¥ÎØ∏ÏßÄ ÏÇ¨Ïù¥Ï¶à : ${imageFile.size / 1024 / 1024} MB`);

    const options = {
      maxSizeMB: 2,
      maxWidthOrHeight: 1920,
    };

    try {
      const compressedFile = await imageCompression(imageFile, options);

      console.log(
        `ÏïïÏ∂ïÎêú Ïù¥ÎØ∏ÏßÄ ÏÇ¨Ïù¥Ï¶à : ${compressedFile.size / 1024 / 1024} MB`
      );

      setIsLoading(false);

      return compressedFile;
    } catch (error) {
      setIsLoading(false);
      console.log(error);
    }
  };

  return { compressImage, isLoading };
};

export default useImageCompress;
